<template>
  <div class="container-fluid">
    <div class="row mt-5">
      <div class="col">
        <p class="col-12 title-section">Administrar Cuenta</p>
        <hr class="my-0 mb-4" />
        <div class="row">
          <div
            class="card col-12 sol-sm-12 col-md-8 col-lg-6 offset-md-2 offset-lg-3 shadow-lg p-3 mb-5 bg-white rounded"
          >
            <div class="card-body">
              <div class="form">
                <label for=""><b>Datos del Correo</b></label>
                <hr class="my-0 mb-4" />
                <div class="form-group">
                  <label for="">Email</label>
                  <input
                    v-model="accountInfo.email"
                    class="form-control"
                    type="email"
                    placeholder="Email"
                  />
                </div>
                <div class="form-group mb-5">
                  <label for="">Password</label>
                  <input
                    v-model="accountInfo.password"
                    class="form-control"
                    type="password"
                    placeholder="Password"
                  />
                </div>
                <label for=""><b>Datos del Servidor SMTP</b></label>
                <hr class="my-0 mb-4" />
                <div class="form-group">
                  <label for="">Servidor SMTP</label>
                  <input
                    v-model="accountInfo.smtpServer"
                    class="form-control"
                    type="text"
                    placeholder="Smtp Server"
                  />
                </div>
                <div class="form-group">
                  <label for="">Puerto</label>
                  <input
                    v-model="accountInfo.smtpPort"
                    class="form-control"
                    type="text"
                    placeholder="Puerto Smtp"
                  />
                </div>
                <div class="form-group">
                  <label for="">Host SMTP</label>
                  <input
                    v-model="accountInfo.smtpHost"
                    class="form-control"
                    type="text"
                    placeholder="Smtp Host"
                  />
                </div>
                <div class="form-group mt-4">
                  <button
                    class="btn btn-dark btn-secondary"
                    @click="updateAccount()"
                    style="background: #1ab187"
                  >
                    <span
                      v-if="this.isLoading"
                      class="spinner-border spinner-border-sm"
                      role="status"
                      aria-hidden="true"
                    ></span>
                    Actualizar
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import config from "../config";
export default {
  data: () => {
    return {
      accountInfo: {
        email: "",
        password: "",
        smtpServer: "",
        smtpPort: 0,
        smtpHost: "",
      },
    };
  },
  computed: {
    isLoading() {
      return this.$store.state.loadingData;
    },
  },
  created() {
    this.$store.commit("SET_LAYOUT", "principal-layout");
  },
  mounted() {
    this.$store.commit("SET_LOADING_DATA", true);
    this.$store
      .dispatch("getAccountInfo")
      .then((account) => {
        this.accountInfo = {
          email: account.email,
          password: account.password,
          smtpServer: account.smtpServer,
          smtpPort: account.smtpPort,
          smtpHost: account.smtpHost,
        };
      })
      .catch((error) => {
        this.$toastr.e(error);
      });
  },
  methods: {
    updateAccount() {
      this.$store.commit("SET_LOADING_DATA", true);
      this.$store
        .dispatch("updateAccountInfo", this.accountInfo)
        .then((rta) => {
          this.$toastr.s("Información actualizada");
          this.$router.push(config.VUE_APP_BASE_URL);
        })
        .catch((error) => {
          this.$toastr.e(error);
        });
    },
  },
};
</script>

<style>
.title-section {
  font-size: 1.5rem;
  /* color: gray; */
  font-weight: bolder;
}

.title-form {
  font-size: 1.5rem;
}
</style>
